.register{
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;

  Form{
    max-width: 280px;
    width: 100%;
    display: flex;
    flex-direction: column;

    Button{
      align-self: center;
      margin-top: 20px;
      max-width: 150px;
      width: 100%;
    }

    .title{
      font-size: 26px;
      text-align: center;
      font-weight: bold;
      margin-bottom: 20px;
    }
  }
}
